import React from "react";
import {
  Nav,
  NavLink,
  Bars,
  NavMenu
  
} from './NavElements';

function Navbar() {

  const customStyle = {
    paddingRight:"480px",
    fontSize:"0.8cm",
    color:"#ffba5a"
    
  }

  return (
    <>
      <Nav style={{position:"fixed"}}>
        <Bars />
    
        <NavMenu>
          <NavLink to="/"  style=  {customStyle }>
            Sogy Hotel
          </NavLink>

          <NavLink to='/welcome' className={"navlink"} activeStyle>
            About Us
          </NavLink>

          <NavLink to='/rooms' className={"navlink"} activeStyle>
            Rooms
          </NavLink>

          <NavLink to='/reservations' className={"navlink"} activeStyle>
            Reservations
          </NavLink>

          <NavLink to='/contactUs' style = {{marginRight:"30px"}} className={"navlink"} activeStyle>
            Contact Us
          </NavLink>

        </NavMenu>

      </Nav>
    </>
  )
};
  
export default Navbar;
  