import React from "react";
import Rotate from "react-reveal/Roll";

function WelcomeButtons(){
    return(
        <div className={"welcome-buttons"}>
         <Rotate>
            <a  href="/"><span >Learn More</span></a>
         </Rotate>
          
          <p>or</p>

          <Rotate left>
            <a  href="/"><span >See video</span></a>
          </Rotate>
        </div>
    )
};

export default   WelcomeButtons;